@import '../Inter.sass';

.Comment {
    display: flex;
    align-items: center;

    margin: 1.1em;

    width: 45%; }

@mixin commentHover($color) {
    background: $color;
    border-color: $color;
    &:hover {
        box-shadow: 4px 4px 30px 2px $color; } }

.Comment-Image {
    @include animate();

    border-radius: 100%;
    border-width: .1em;
    border-style: solid;

    margin: 0 1.1em 1.1em 0;

    $size: 100px;

    min-width: $size;
    min-height: $size;

    width: $size;
    height: $size;

    object-fit: cover;

    &:hover {
        transform: translate(-4px, -4px); }

    &_side_dark {
        @include commentHover($pink); }
    &_side_light {
        @include commentHover($cyan); }
    &_side_neutrally {
        @include commentHover($yellow); } }

.Comment-Quote {
    font-style: italic; }

.Comment-Author {
    text-transform: uppercase;

    font-size: 1em;
    font-weight: bolder;

    margin: .5em 0; }

.Comment-Person {
    &_side_light {
        color: $cyan; }
    &_side_dark {
        color: $pink; }
    &_side_neutrally {
        color: $yellow; } }

@media (max-width: 1000px) {
    .Comment {
        width: 100%; } }


@media (max-width: 460px) {
    .Comment-Image {
        $size: 70px;

        min-width: $size;
        min-height: $size;

        width: $size;
        height: $size; }

    .Comment-Quote {
        font-size: 12px; }

    .Comment-Person {
        font-size: 16px; } }
