.Paginator {
    margin-top: 3em;

    position: relative;

    display: flex;
    justify-content: center;
    align-items: center; }

.Paginator-Arrow {
    position: absolute;

    padding: 1em;
    cursor: pointer; }

$offset: -40%;

.Paginator-Arrow_side_next {
    right: $offset; }

.Paginator-Arrow_side_prev {
    transform: rotate(180deg);
    left: $offset; }

@media (max-width: 410px) {
    .Paginator-Arrow {
        $offset: -25%;

        &_side_next {
            right: $offset; }
        &_side_prev {
            left: $offset; } } }
