@import '../Inter.sass';

.Additional {
    text-align: center;

    width: 100%;

    padding: 1em 0;

    font-weight: bold;

    display: flex;
    justify-content: center;
    align-items: center;

    border-width: .1em;
    border-color: transparent;

    &_side_dark {
        @include addity($pink, $white); }
    &_side_light {
        @include addity(transparent, $cyan, 'bordered'); }
    &_side_neutrally {
        @include addity(transparent, $yellow, 'bordered'); } }
