@import '../Inter.sass';

.Header {
    z-index: 1000;

    position: fixed;

    top: 0;
    left: 0;

    width: 100%;

    display: flex;
    justify-content: center; }

.Header-Container {
    position: relative;

    display: flex;
    justify-content: space-around; }

.Header-Nav {
    border-radius: 100%;

    @include animate();

    opacity: 0;

    width: 100vw;
    height: 100vh;

    position: fixed;
    top: 0;
    left: 0;

    transform: scale(0);

    display: grid;

    grid-template-columns: repeat(auto-fill, 238px);

    grid-auto-columns: min-content;
    grid-auto-rows: 78px;

    grid-gap: 20px;

    align-content: center;
    justify-content: center;

    &_side_light {
        background-color: $cyanTrans; }
    &_side_dark {
        background-color: $pinkTrans; }
    &_side_neutrally {
        background-color: $yellowTrans; } }

.Header-NavItem {
    @include animate();

    font-size: 1.2em;

    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
        transform: translate(-2px, -2px);

        box-shadow: 5px 5px 2px 2px rgba(0, 0, 0, .5); }

    &_side_light {
        background: $black; }
    &_side_neutrally {
        background: $black; } }

.Header-Nav_active {
    opacity: 1;

    border-radius: 0;

    transform: scale(1); }

.Burger {
    z-index: 100000;

    position: fixed;

    top: 20px;
    right: 20px;

    width: 40px;

    display: flex;
    flex-direction: column;

    &:before, &:after, .Burger-Inner {
        @include animate();

        content: '';
        display: block;

        border-radius: 2px;

        width: 100%;
        height: 2px; }

    .Burger-Inner {
        margin: 10px 0; }

    &_side_neutrally {
        &:before, &:after, .Burger-Inner {
            background: $yellow; } }
    &_side_light {
        &:before, &:after, .Burger-Inner {
            background: $cyan; } }
    &_side_dark {
        &:before, &:after, .Burger-Inner {
            background: $pink; } } }

.Burger_active {
    .Burger-Inner {
        opacity: 0; }
    &:before {
        transform: translate(0, 12px) rotate(45deg); }
    &:after {
        transform: translate(0, -12px) rotate(-45deg); } }
